import React,{Component} from 'react'
import {Link} from 'react-router-dom'

function Login (){
  return(
    <div className="login">
      <div className='log_card'>
        <p className="log_title">Log in</p>

        <form className="log_form">

          <div className="log_field">
            <input type='email' placeholder="Email" required></input>
            <div className="underline"></div>
          </div>

          <div className="log_field">
            <input type='password' placeholder="Password" required></input>
            <div className="underline"></div>
          </div>

          <p className="log_pass">Forgot password?</p>
          <Link to="/"><button className="log_button">Continue</button></Link>
          <div className="log_signup">New to e-commerce? <Link to="/signup">Sign up</Link></div>
        </form>
      </div>
    </div>
  )
}


export default Login; 